<template>
    <div>
        <HeaderTop />
        <div class="favorMain">
            <router-link to="/">首页</router-link> / <span>收藏</span>
        </div>
        <div class="main-text">
            <ul>
                <li v-for="list in lists " :key="list.id">

                    <router-link :to="{ name: 'userDetails', params: { userName: list.author.loginname } }"><span
                            class="head"><img :src="list.author.avatar_url" alt=""></span></router-link>

                    <router-link style="width:1000px;padding-left:20px;"
                        :to="{ name: 'details', params: { topicId: list.id } }">
                        {{ list.title }} <span>{{ list.tab }}</span>
                    </router-link>
                    <el-button @click="Uncollect(list.id)">取消收藏</el-button>
                </li>
            </ul>
        </div>
        <div class="fovarbot">

        </div>
    </div>
</template>
<script>
import HeaderTop from '../components/HeaderTop.vue';
export default {
    components: { HeaderTop },
    data() {
        return {
            lists: []
        }
    },
    async created() {
        let login = JSON.parse(sessionStorage.getItem('userInfo')).loginname
        const res = await this.$http.get(`/topic_collect/${login}`)
        this.lists = res.data
        console.log(this.lists)
    },
    methods: {
        async Uncollect(id) {
              await this.$http({
                method: 'post',
                url: '/topic_collect/de_collect',
                data: {
                    accesstoken: sessionStorage.getItem('token'),
                    topic_id: id
                }
            })
            this.lists= this.lists.filter((ele) => {
              return  ele.id !==id 
            })
        },
    }
}

</script>
 
<style>
.favorMain {
    width: 1100px;
    margin: 0 auto;
    padding: 10px;

    margin-top: 20px;
    background-color: #eee;
    box-sizing: border-box;
    border-radius: 5px 5px 0px 0px;
    text-align: left;
    border-bottom: 1px solid #ccc
}

.favorMain a {
    text-decoration: none;
    color: #000;
}

.main-text {
    width: 1100px;
    margin: 0 auto;
    padding: 10px;
    padding-right: 300px;
    background-color: #fff;
    box-sizing: border-box;
    text-align: left;

}

.main-text ul {
    width: 100%;
}

.main-text ul li {
    display: flex;
    width: 1090px;
}

.main-text ul li img {
    display: inline-block;
    width: 30px;
    height: 30px;
    vertical-align: middle;
}

.main-text ul li a:first {
    width: 30px;
    text-align: center;
}

.main-text ul li a {
    display: block;
    box-sizing: border-box;
    background-color: #fff;
    line-height: 50px;
    border-bottom: 1px solid #ccc;
    cursor: pointer;
    text-decoration: none;
    color: #000;

}

.main-text ul li a:hover {
    background-color: rgb(246, 246, 246);
    text-decoration: underline;
}

.main-text ul li a span {
    background-color: #ccc;
    border-radius: 5px;
    padding: 0 3px;
    margin-left: 5px;
    display: inline-block;
    line-height: 20px;
}

.main-text ul li a span:hover {
    text-decoration: none;
}

.fovarbot {
    width: 1100px;
    margin: 0 auto;
    padding: 15px;
    background-color: #fff;
    box-sizing: border-box;
    border-radius: 0px 0px 5px 5px;
    text-align: left;
}
</style>